@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(100, 100, 100, 0.5);
}

.loading-loader {
  position: relative;
  top: 45%;
}


@media only screen and (max-width: 600px) { /* Specific to this particular image */

    img.login-background {
        z-index: -1;
        /* Set rules to fill background */
        min-height: 100%;
        min-width: 1024px;

        /* Set up proportionate scaling */
        width: 100%;
        height: auto;

        /* Set up positioning */
        position: fixed;

        /* Transform the image */
        -webkit-transform: translate(-4%, -15%) scale(.8);
                transform: translate(-4%, -15%) scale(.8);

    }

    .main-login-container {
        display: flex;
        justify-content: center;
    }

    .login-div {
        position: absolute;
        top: 35%;
        bottom: 10%;
        width: 80%;
        height: 50%;
        border-radius: 20px;
        background-color: rgba(255,255,255,.6);
        box-shadow: 1px 0 20px black;
    }

}




@media screen and (min-width: 577px) {

    img.login-background {
        z-index: -1;

        /* Set up proportionate scaling */
        width: 130%;
        height: auto;

        /* Set up positioning */
        position: fixed;

        /* Transform the image */
        -webkit-transform: translate(-6%, -14%)scale(.9);
                transform: translate(-6%, -14%)scale(.9);

    }

    .login-title {
        font-size: xx-large ;
    }
    .input-host, .input-password, .input-username {
        font-size: larger;
    }

    .main-login-container {
        display: flex;
        justify-content: center;
    }

    .login-div {
        position: absolute;
        top: 35%;
        bottom: 10%;
        width: 25%;
        height: 55%;
        border-radius: 20px;
        background-color: rgba(255,255,255,.6);
        box-shadow: 1px 0 20px black;
    }

    .img-bottom-part {
        position: absolute;
        top: 30em;
        left: 0;

    }


}

.img-bottom-part {
    position: fixed;
    bottom: 0;
    left: 0;
}





.login-title {
    font-weight: bolder;
    margin-bottom: 25px;
}

.input-container {
    overflow: hidden;
    margin-top: 3.2em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.input-host, .input-password, .input-username{
    display: block;
    width: 90%;
    margin: 10px 3% !important;
    border-radius: 8px;
    border: none;
    padding-left: .5em;
    background: #e0e0e0;
    font-weight: lighter;
    color: black;

}
.input-username::-webkit-input-placeholder, .input-password::-webkit-input-placeholder{
    color: #a9a9a9;
    font-weight: lighter;

}
.input-username::-ms-input-placeholder, .input-password::-ms-input-placeholder{
    color: #a9a9a9;
    font-weight: lighter;

}
.input-username::placeholder, .input-password::placeholder{
    color: #a9a9a9;
    font-weight: lighter;

}


.login-button {
    display: block;
    width: 40%;
    margin: 25px 3% !important;
    border-radius: 5px;
    transition: ease-in-out .4s;
}
.login-button:active {
    background: #980518;
}



.img-logo {
    position: relative;
    width: 50%;
    margin-top: 3em;
}

.top-part {
    height: 2.5em;
    width: 100%;
    position: relative;
    border-radius: 8px 8px 0 0;
    background: #e30518;
}
.navbar-brand {
    color: darkgrey !important;
    font-weight: bold;
}

.nav-item {
    cursor: pointer !important;
}

.nav-link:hover, .navbar-brand {
    cursor: pointer !important;
}

.app-nav-div{
    width: 100%;
    height: 77px;
    position: absolute;
}

.icon {
    font-size: xx-large;
    width: 100%;
}

.bell-icon {
    color: darkgrey;
    margin: 4px 0;
}

.profile-div {
    position: relative;
    height: 40px;
    width: 40px;
    margin-left: 20px;
}

.profile-img {
    object-fit: cover;
    top: 0;
    left: 0;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    position: absolute;
}

.profile-active {
    border-radius: 50%;
    background-color: green;
    position: absolute;
    bottom: 0;
    right: 0;
    height: 12px;
    width: 12px;
}

.dots:after {
    content: '\2807';
    height: 42px;
    color: darkgrey;
    font-size: 27px;
    margin-left: 20px;
}

.dropdown-menu-right {
    margin-top: 13px !important;
}

.app-nav-tab {
    background-color: lightgray;
    padding-top: 10px;
    text-align: center;
    color: darkslategray;
    cursor: pointer;
    /*width: 20%;*/
    width: 50%;
    display: inline-block;
    border: 0 solid white;
    border-right-width: 1px;
    border-left-width: 1px;
}

.app-nav-tab-active {
    background-color: #919191;
    padding-top: 10px;
    text-align: center;
    color: darkslategray;
    cursor: pointer;
    /*width: 20%;*/
    width: 50%;
    display: inline-block;
    border: 0 solid white;
    border-right-width: 1px;
    border-left-width: 1px;
}

.app-nav-tab-title {
    width: 100%;
    font-size: 5vw;
}

@media only screen and (min-width: 576px){
    .app-nav-tab-title {
        width: 100%;
        font-size: larger;
    }
}
.totals-data {
    border: 3px solid;
    margin: 2%;
    transition: all 1s ease;
    border-radius: 5px;
    font-size: larger;
}

.totals-select {
    border: 2px solid;
    width: 100%;
    transition: all 1s ease;
    font-size: x-large;
    border-radius: 5px;
}
.ranking-item {

}

/* simple, float it right instead of left */
.progress-right .progress-bar {
    position: relative;
    border-radius: 20px;
    width: 10%;
    margin-left: 90%    ;
    transition: all 1s ease;
}

.progress {
    height: 40px !important;
    border-radius: 20px !important;
}

.ranking-item-title {
    margin-left: 10px;
    margin-bottom: 0;
    font-size: smaller;
    display: block;
}

.ranking-item-sub-title {
    margin-left: 10px;
    margin-bottom: 0;
    font-size: x-small;
    display: block;
    color: gray;
}

.ranking-item-number {
    transition: all 1s ease;
}

.inverse-progress-bar-value {
    color: #7b7b7b;
    position: absolute;
    top: 22%;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: medium;
    font-weight: bolder;
}
.comparative-container {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.user_info {
  height: 75vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%; }

.parte-alta {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  position: absolute;
  left: 0;
  top: 0; }

.parte-baja {
  position: absolute;
  bottom: 0;
  left: 0; }

.greet {
  margin-top: 1em;
  color: red;
  font-weight: bolder; }

.message_to_user {
  font-size: 1.4em;
  font-weight: lighter; }

.dropdown-comparativo {
  position: relative;
  width: 90%;
  height: 6%;
  background: red;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .dropdown-comparativo p {
    margin: 0;
    font-size: 1.4em;
    color: white;
    font-weight: lighter;
    letter-spacing: 2px; }

.data-visualizer {
  width: 90%;
  height: 65%;
  background: #ebebeb;
  -webkit-animation: fadeIn 500ms;
  animation: fadeIn 500ms;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5%;
  padding: 1em; }

.close-session {
  width: 90%;
  height: 10%;
  border-radius: 5px;
  border: 2px solid red;
  display: flex;
  justify-content: center;
  align-items: center; }
  .close-session p {
    margin: 0;
    font-size: 1.6em;
    font-weight: bolder;
    color: #e30518; }

.logo-header {
  position: absolute;
  -webkit-transform: scale(0.6);
  transform: scale(0.6);
  z-index: 100;
  top: 1%; }

.comparative-select {
  width: 100%;
  transition: all 1s ease;
  font-size: large;
  border-radius: 5px;
  border: none;
  padding: .4em;
  background: white;
  display: flex;
  justify-content: center; }

.comparative-select-col {
  padding: 0 1% !important; }

@-webkit-keyframes fadeIn {
  from {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0; }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1; } }

@keyframes fadeIn {
  from {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0; }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1; } }

@media screen and (min-width: 577px) {
  .parte-alta {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    position: absolute;
    left: 0;
    top: -25%; }
  .parte-baja {
    position: absolute;
    bottom: -28%;
    left: 0; }
  .logo-header {
    position: absolute;
    -webkit-transform: scale(0.2);
    transform: scale(0.2);
    z-index: 100;
    top: -6%; }
  .user_info {
    height: 75vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 50%;
    margin-top: 3%; } }

.splash-main-container {
    position: absolute;
    width: 100%;
    height: 100vh;
    overflow: hidden;

    display: grid;
    grid-template-rows: 1fr 2fr
}

.main-logo-container {
    width: 100%;
    background: #e30518;
    display: flex;
    flex-direction: column;
    justify-content: center;

}


.main-img {
    /*box-shadow: 1px 0 30px black;*/
    position: relative;
    z-index: 1;
    width: 100%;
}

.main-logo {
    -webkit-transform: scale(.8);
            transform: scale(.8);

}
.subtitle {
    font-weight: bolder;
    color: white;
}

.irregular-shape1 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3em;
    background: #b30000;
    z-index: 100;
    box-sizing: border-box;
}

.irregular-shape2 {
    position:absolute;
    bottom: 6%;
    right: -28%;
    width:250px;
    height:120px;
    overflow:hidden;
    z-index: 0;
    background: #b30000;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);

}

.irregular-shape2:before,.irregular-shape2:after{
    content: '';
    position: absolute;
}
.irregular-shape2:before{

    right: -48%;
    top: -1%;
    background: #e30518;
    width: 270px;
    height: 200px;
    border-radius: 100%;
}

.patch1, .patch2 {
    position: absolute;
    z-index: 50;
    background: #e30518;
}
.patch1 {
    width: 2em;
    height: 12.5em;
    bottom: -6%;
    right: 32%;
    background: #e30518;
}
.patch2 {
    width: 12em;
    height: 2em;
    right: 0;
    bottom: 21%;
}
